import React, { Component } from "react";
import { Tooltip, Modal, Button, Tabs, Tree, Radio } from '@platform/base';
import { isFunction, getSysFieldid } from '@platform/api';
// import Filed from './Filed';
import DragItem from './dragItem';
import withDragDropContext from "./withDragDropContext";
import LeftArea from './normalArea';
import SuperArea from './superArea';
import { setOperationOptionsMultiLang } from './utils';

const { TabPane } = Tabs;
const { TreeNode } = Tree;
const { RadioGroup } = Radio;

require('./highPanel.less')
class HighPanel extends Component {
    modalShow = () => {
        const { search } = this.props;
        const { setPanelShow } = search.getInnerHooks();
        setPanelShow(true)
    }

    modalClose = () => {
        const { search } = this.props;
        const { setPanelShow } = search.getInnerHooks();
        setPanelShow(false)
    }

    // 
    renderLeftContent = () => {

    }

    // 
    renderRightContent = () => {
        const { search, config = {} } = this.props;
        const { getJson } = search.getInnerHooks();
        const { addAdvTabs } = config;
        const multiLang = getJson();
        let def = [
            {
                name: multiLang['page-search-0025'],
                content: this.defRightDom(),
            },
        ];
        if (isFunction(addAdvTabs)) {
            def = [...def, ...addAdvTabs()];
        }
        return def;
    }

    // 
    defRightDom = () => {
        const { search, config = {} } = this.props;
        const { getJson, getHighPanelStatus, setHighPanelStatus, initSuperData } = search.getInnerHooks();
        const { addAdvBody } = config;
        const multiLang = getJson();
        let status = getHighPanelStatus();
        // initSuperData()
        return (
            <div className="adv-right-area" id="adv-right-area">
                {isFunction(addAdvBody) && addAdvBody()}
                <RadioGroup selectedValue={status} onChange={item => setHighPanelStatus(item)}>
                    <Radio value='normal'>{multiLang['page-search-0006']}</Radio>
                    <Radio value='super'>{multiLang['page-search-0007']}</Radio>
                </RadioGroup>
                {status === 'normal' && (
                    <LeftArea
                        search={search}
                        addAdvBody={addAdvBody}
                    />
                )}
                {status === 'super' && (
                    <SuperArea
                        search={search}
                        addAdvBody={addAdvBody}
                    />
                    // <div 
                    //     id="advArea" 
                    //     className="superArea" 
                    //     style={{ height: isFunction(addAdvBody) ? 'auto' : 'calc(100% - 40px)' }}
                    // >
                    //     {this.creatRightCondition()}
                    // </div>
                )}
            </div>
        );
    }

    creatRightCondition = () => {

        return <div>1234</div>
    }
    // createTree tree 
    createTree = () => {
        const { search } = this.props;
        const { getStore } = search.getInnerHooks();
        const items = getStore(['items']);
        if (!items) return null
        return (
            Object.values(items).map(item => {
                return (
                    <DragItem
                        items={item}
                        key={item.attrcode}
                    />
                )
            })
        )
    }
    // 
    createSearchPlan = () => {
        const { search } = this.props;
        const { getJson, getStore, getCurrentHighPlan, setCurrentHighPlan } = search.getInnerHooks();
        const multiLang = getJson();
        const querySchemes = getStore(['querySchemes']);
        let currentPlan = getCurrentHighPlan();
        if (!querySchemes) return null
        return querySchemes.map(item => {
            return (
                <li className={`searchPlanList ${currentPlan == item.pk_ncc_queryscheme ? 'active' : ''}`} key={item.pk_ncc_queryscheme} >
                    <span className="searchPlanName" onClick={() => setCurrentHighPlan(item.pk_ncc_queryscheme)}>
                        <Tooltip
                            delayShow={800}
                            inverse
                            overlay={<div>{item.name}</div>}
                            placement="top"
                        >
                            <i>{item.name}</i>
                        </Tooltip>
                    </span>

                    {!item.isquickqs && !item.isMyPlan && (
                        <Tooltip
                            placement="top"
                            overlay={multiLang['page-search-icon-modify']}
                        >
                            <span className="searchPlanEdit" onClick={() => { }}>
                                <i className='iconfont icon-gerenpeizhi' />

                            </span>
                        </Tooltip>
                    )}

                    {!item.isquickqs && !item.isMyPlan && (
                        <Tooltip
                            placement="top"
                            overlay={multiLang['page-search-icon-delete']}
                        >
                            <span className="searchPlanDel" onClick={() => { }}>
                                <i className='iconfont icon-shanchu' />
                            </span>
                        </Tooltip>
                    )}
                </li>
            )
        })
    }

    // render content
    renderContent = () => {
        const { search, config = {} } = this.props;
        const { getLeftActiveTab, setLeftActiveTab, getRightActiveTab, setRightActiveTab, getJson } = search.getInnerHooks();
        const { replaceRightBody } = config;
        const activeTab = getLeftActiveTab();
        const multiLang = getJson();
        return (
            <div className="modalBody">
                <div className="modalBodyBox">
                    <div className="leftArea">
                        <Tabs
                            defaultActiveKey="1"
                            onChange={item => setLeftActiveTab(item)}
                            activeKey={activeTab}
                            tabBarStyle="noborder"
                            className="searchTabStyle"
                        >
                            <TabPane
                                tab={(
                                    <div className={activeTab == '1' && 'tab-label-in'}>{multiLang['page-search-0004']}</div>
                                )}
                                key="1"
                            >
                                {this.createTree()}
                            </TabPane>
                            <TabPane
                                tab={(
                                    <div className={activeTab == '2' && 'tab-label-in'}>{multiLang['page-search-0005']}</div>
                                )}
                                key="2"
                            >
                                <ul className="searchPlanUl">{this.createSearchPlan()}</ul>
                            </TabPane>
                        </Tabs>
                    </div>
                    <div className="rightArea">
                        {isFunction(replaceRightBody) && replaceRightBody()}
                        {!isFunction(replaceRightBody) && (
                            <span className="clearData" onClick={() => { }}>
                                {multiLang['page-search-0017']}
                            </span>
                        )}
                        {!isFunction(replaceRightBody) && (
                            <Tabs style={{ height: '100%' }} onChange={item => setRightActiveTab(item)} activeKey={getRightActiveTab()}>
                                {this.renderRightContent().map((e, i) => {
                                    if (e.defaultOpen === true && this.defaultOpen) {
                                        setRightActiveTab(String(i));
                                        // this.defaultOpen = false;
                                    }
                                    return (
                                        <TabPane tab={e.name} key={i}>
                                            {e.content}
                                        </TabPane>
                                    );
                                }, this)}
                            </Tabs>
                        )}
                    </div>
                </div>
            </div>

        )
    }

    render() {
        const { search } = this.props;
        const { getStore, getJson } = search.getInnerHooks();
        const multiLang = getJson();
        let panelShow = getStore(['showHighPanel']);
        return (
            <Modal show={panelShow} onHide={this.modalClose} width={850}>
                <Modal.Header closeButton>
                    <Modal.Title>
                        {multiLang['page-search-0025']}
                    </Modal.Title>
                </Modal.Header>

                <Modal.Body className="NC_searchAdvModal">
                    {this.renderContent()}
                </Modal.Body>

                <Modal.Footer>
                    <Button
                        onClick={this.savePlanFun}
                        shape="border"
                        //colors="danger"
                        className="secondary-button secondary-button-wrapper footer-button-left"
                        fieldid="savePlan"
                    >
                        {multiLang['page-search-0008']}
                    </Button>
                    <Button
                        onClick={this.addPlanFun}
                        shape="border"
                        //colors="danger"//
                        className="secondary-button secondary-button-wrapper footer-button-right"
                        fieldid="addPlan"
                    >
                        {multiLang['page-search-0081']}
                    </Button>
                    <Button onClick={this.modalClose} bordered>
                        {multiLang['page-search-0001']}
                    </Button>
                    <Button onClick={this.modalClose} colors="secondary" style={{ marginRight: 8 }}>
                        {multiLang['page-search-0010']}
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

export default withDragDropContext(HighPanel)