import { BaseStore, hookFactory, ArrayData } from '@platform/template';
import { ViewModel, toast } from '@platform/api';
import { 
    planExtend,
    initDefaultSnapshot,
    ArrayHas2Value,
    isDateFunction,
    getFunctionValueFromViewModal,
    naturalTimeHandler,
    CompareDate,
    getTimezoneOffset,
    setValue,
    clearAllValueEve,
    plan2snapshotTranslate, 
    setOperationOptionsMultiLang,
    initSuperData,
} from './utils';
import { isArray, isEmpty, timeFunctionTranslater, DongbaToLocalTime, LocalToDongbaTime } from '@platform/api';
import { moment } from '@platform/api';

class SearchStore extends BaseStore {

    store = { }

    meta = {}
    hasRender = false

    // pk
    presetScheme = 'preset';

    // api
    getHooks = () => {
        return {
            getInnerHooks: this.getInnerHooks,
            getItemValue: this.getItemValue,
            setItemValue: this.setItemValue,
            setMeta: this.setMeta,
            getAllSearchData: this.getAllSearchData,
            getQueryInfo: this.getQueryInfo,
            setSearchValByField: this.setSearchValByField,
            setItemsAttribute: this.setItemsAttribute,
            getItemsAttribute: this.getItemsAttribute,
            setSearchValue: this.setSearchValue,
            getSearchValByField: this.getSearchValByField,
            setDisabledByField: this.setDisabledByField,
            closeSearchPlanArea: this.closeSearchPlanArea,
            openAdvSearch: this.openAdvSearch,
            setDisabled: this.setDisabled,
            clearSearchArea: this.clearSearchArea,
            getOprtypeByField: this.getOprtypeByField,
            changeItemVisibleByField: this.changeItemVisibleByField,
            setRequiredByField: this.setRequiredByField,
            setTemlateByField: this.setTemlateByField,
            getStore: this.getStore,
        }
    }

    //  api
    getInnerHooks = () => {
        return {
            getCtx: this.getCtx,
            setCtx: this.setCtx,
            setInitialValues: this.setInitialValues,
            getSearchItemDom: this.getSearchItemDom,
            getCurrentPlan: this.getCurrentPlan,
            setCurrentPlan: this.setCurrentPlan,
            countUIScheme: this.countUIScheme,
            setJson: this.setJson,
            getJson: this.getJson,
            setStore: this.setStore,
            getStore: this.getStore,
            setPanelShow: this.setPanelShow,
            getLeftActiveTab: this.getLeftActiveTab,
            setLeftActiveTab: this.setLeftActiveTab,
            getRightActiveTab: this.getRightActiveTab,
            setRightActiveTab: this.setRightActiveTab,
            getHighPanelStatus: this.getHighPanelStatus,
            setHighPanelStatus: this.setHighPanelStatus,
            getCurrentHighPlan: this.getCurrentHighPlan,
            setCurrentHighPlan: this.setCurrentHighPlan,
            setShowSearchItems: this.setShowSearchItems,
            countHighUIScheme: this.countHighUIScheme,
            getAllSearchDataEve: this.getAllSearchDataEve,
            clearAllValueEve: this.clearAllValueEve,
            setItemOperationSign: this.setItemOperationSign,
            initSuperData: this.initSuperData,
        }
    }

    // ============= api ===============
    
    getCtx = () => {
        return this.ctx
    }

    setCtx = ctx => {
        this.ctx = ctx;
        return true
    }
    
    /**
     * @description: store 
     * @param {type} 
     * @return: 
     */    
    setStore = (path, value, shouldUpdate = true) => {
        let newValue = this.set(path, value);
        shouldUpdate && this.forceUpdate()
        return newValue 
    };
    /**
     * @description: store
     * @param {type} 
     * @return: 
     */    
    getStore = path => {
        return this.get(path)
    }
    /**
     * @description:   
     * @param {type} 
     * @return: 
     */    
    setPanelShow = (value, pk) => {
        this.setStore(['showHighPanel'], value, false);
        this.setStore(['activeLeftTab'], '2', false)
        this.setStore(['currentHighPlan'], pk || this.get(['currentPlan']), false);
        this.setCurrentHighPlan(pk || this.get(['currentPlan']), false);
        if(value && (!pk || pk == this.get(['currentPlan']))){
            let currentPlanList = this.get(['currentPlanMap'])
            // uniqueKey   index 
            for(let i =0;i<currentPlanList.length;i++){
                this.get(['currentHighPlanMap']).update(i, res => {
                    res['initialvalue'] = Array.isArray(currentPlanList[i]['initialvalue']) ? [...currentPlanList[i]['initialvalue']] : { ...currentPlanList[i]['initialvalue'] }
                    return res
                })
            }
        }
        this.forceUpdate();
    }
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setJson = json => {
        this.setStore(['json'], json);
        setOperationOptionsMultiLang(json);
    }
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    getJson = () => {
        return this.get(['json']) || {}
    }
    /**
     * @description: tab
     * @param {type} 
     * @return: 
     */    
    getLeftActiveTab = () => {
        return this.get(['activeLeftTab'])
    }
    /**
     * @description: tab
     * @param {type} 
     * @return: 
     */    
    setLeftActiveTab = value => {
        this.setStore(['activeLeftTab'], value)
    }
    /**
     * @description: tab
     * @param {type} 
     * @return: 
     */   
    getRightActiveTab = () => {
        return this.get(['activeRightTab'])
    }
    /**
     * @description: tab
     * @param {type} 
     * @return: 
     */   
    setRightActiveTab = value => {
        this.setStore(['activeRightTab'], value)
    }
    /**
     * @description:  normal super 
     * @param {type} 
     * @return: 
     */    
    getHighPanelStatus = () => {
        return this.get(['highPanelStatus'])
    }
    /**
     * @description:  normal super 
     * @param {type} 
     * @return: 
     */    
    setHighPanelStatus = value => {
        this.setStore(['highPanelStatus'], value);
    }
    /**
     * @description: UI pk UI 
     * @param {type} pk
     * @return: []
     */    
    countUIScheme = () => {
        return this.get(['currentPlanMap']);
    }
    /**
     * @description: pk
     * @param {type} 
     * @return: 
     */    
    getCurrentPlan = () => {
        return this.get(['currentPlan'])
    }
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setCurrentPlan = pk => {
        let currentList = []
        let querySchemes = this.get(['querySchemes']).query(pk);
        currentList = querySchemes['renderobj4web'];
        if(pk !== 'preset'){
            currentList = new ArrayData(this.get(['fixedConditionGroup']).concat(planExtend(this.get(['items']), currentList, this.get(['context']))), 'uniqueKey');
        }else {
            currentList = new ArrayData(initDefaultSnapshot(this.get(['items']), this.get(['context']), this.get(['isTemplateOrder'])), 'uniqueKey');
        }

        this.setStore(['currentPlan'], pk, false);
        this.setStore(['currentPlanMap'], currentList);
    }
    /**
     * @description: PK
     * @param {type} 
     * @return: 
     */    
    getCurrentHighPlan = () => {
        return this.get(['currentHighPlan'])
    }
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setCurrentHighPlan = pk => {
        // this.setStore(['currentHighPlan'], value)
        let currentList = []
        let querySchemes = this.get(['querySchemes']).query(pk);
        currentList = querySchemes['renderobj4web'];
        if(pk !== 'preset'){
            currentList = new ArrayData(this.get(['fixedConditionGroup']).concat(plan2snapshotTranslate(this.get(['items']), currentList, this.get(['context']))), 'uniqueKey');
        }else {
            currentList = new ArrayData(initDefaultSnapshot(this.get(['items']), this.get(['context']), this.get(['isTemplateOrder'])), 'uniqueKey');
        }

        this.setStore(['currentHighPlan'], pk, false);
        this.setStore(['currentHighPlanMap'], currentList);
        this.initSuperData();
        console.log(this.store)
    }

    /**
     * @description:  UI 
     * @param {type} 
     * @return: 
     */    
    countHighUIScheme = () => {
        if(this.get(['highPanelStatus']) === 'normal'){
            return this.get(['currentHighPlanMap']);
        }else {
            return this.get(['currentHighPlanSuperMap']);
        }
        
    }
    /**
     * @description: meta  
     * @param {type} 
     * @return: 
     */    
    setInitialValues = async (meta, searchId, app_context) => {
        await Promise.resolve(() => {});
        if(!meta || Object.keys(meta).length === 0) return;
        const { items = [], oid, queryschemes = [], moduletype } = meta[searchId];
        let config = (this.ctx[0] && this.ctx[0].props.config) || {};
        let store = {},
            presetScheme = {
                isquickqs: false,
                isDefault: true,
                isMyPlan: true,
                name: '',
                type: true,
                renderobj4web: [],
                pk_ncc_queryscheme: this.presetScheme,
            },
            fixedConditionGroup = {},
            context = {},
            propsContext = config.context || '',
            isTemplateOrder = config.isTemplateOrder || false,
            currentPlanMap = [];

        if(moduletype === 'search'){
            
            // context 
            context = { ...ViewModel.getContext(), ...(propsContext || app_context) };

            currentPlanMap = new ArrayData(initDefaultSnapshot(items, context, isTemplateOrder), 'uniqueKey');
            presetScheme.renderobj4web = currentPlanMap;
            fixedConditionGroup = currentPlanMap.filter(item => item.isExtend) || [];

            //  
            queryschemes.unshift(presetScheme);
            store['items'] = new ArrayData(items, 'attrcode');
            store['querySchemes'] = new ArrayData(queryschemes, 'pk_ncc_queryscheme');
            store['fixedConditionGroup'] = fixedConditionGroup;
            store['currentPlan'] = queryschemes.find(item => item.isquickqs == true) ? queryschemes.find(item => item.isquickqs == true)['pk_ncc_queryscheme']:this.presetScheme;
            // store['currentPlanMap'] = currentPlanMap;
            store['showHighPanel'] = false;
            store['activeLeftTab'] = '2';
            store['activeRightTab'] = '0';
            store['highPanelStatus'] = 'normal';
            // store['currentHighPlan'] = this.presetScheme;
            // store['currentHighPlanMap'] = currentPlanMap;
            store['context'] = context;
            store['oid'] = oid;
            store['showSearchItems'] = false;
            store['isTemplateOrder'] = isTemplateOrder;
            store['searchId'] = searchId;
        }
        this.store = Object.assign(this.store, store);
        this.setCurrentPlan(this.getStore(['currentPlan']))
        // this.initSuperData();
        // this.setCurrentHighPlan(this.getStore(['currentHighPlan']))
        console.log(this.store)
    }
    /**
     * @description: item
     * @param {type} 
     * @return: 
     */    
    getSearchItemDom = key => {
        return this.get(['items']).query(key)
    }

    /**
     * @description:  
     * @param {type} 
     * @return: 
     */    
    setShowSearchItems = status => {
        this.setStore(['showSearchItems'], status)
    }

    /**
     * @description: 
     * @param {type} 
     * @return {type} 
     */    
    initSuperData = status => {
        let items = this.getStore(['items']);
        let newItems = [];
        /*  */
        let filterMeta = data => {
            data.map(item => {
                if (Array.isArray(item.children)) {
                    filterMeta(item.children);
                }
                if (item.visible || item.isfixedcondition) {
                    //item.advrequired = !!item.required;
                    newItems.push(item);
                }
            });
        };
        filterMeta(items);
        let superData = initSuperData(newItems);
        console.log(superData)
        this.setStore(['currentHighPlanSuperMap'], superData)
    }

    // =============== api ===============
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    getItemValue = (planName, uniqueKey, key = 'initialvalue') => {
        if(planName == 'currentHighPlanSuperMap') return { value: '', display: '' }
        return this.get([planName]).query(uniqueKey)[key]
    }
    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setItemValue = (planName, uniqueKey, value, attrcode, attribute = 'initialvalue') => {
        if(planName == 'currentHighPlanSuperMap') return { value: '', display: '' }
        this.get([planName]).update(uniqueKey, res => {
            if(attrcode){
                res['valueMap'][attrcode] = { ...value };
                res[attribute] = { ...value }
            }else {
                res[attribute] = { ...value };
            }
            
            return res
        })
        this.updateItem([uniqueKey])
        console.log(this.get([planName]).query(uniqueKey));
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setItemOperationSign = (planName, uniqueKey, value, oldValue) => {
        this.get([planName]).update(uniqueKey, res => {
            if (value !== 'between' && oldValue === 'between') {
                res.initialvalue.value = '';
                res.initialvalue.display = '';
            } else if (value === 'between' && oldValue !== 'between') {
                res.initialvalue.display = [];
                res.initialvalue.value = [];
            }
            res['operationSign'] = value;            
            return res
        })
        // this.updateItem([uniqueKey])
        this.forceUpdate()
        console.log(this.get([planName]).query(uniqueKey));
    }

    setMeta = async (meta, id, flag, app_context) => {
        // this.setStore(['meta'], meta, false);
        let currentID = id || this.getStore('mainId')
        await this.setInitialValues(meta, currentID, {}, app_context);
        this.forceUpdate()
    }

    /**
     * @description: item
     * @param {type} 
     * @return: 
     */    
    setItemsAttribute = (field, attribute, status) => {
        this.getStore(['items']).query(field).update(res => {
            res[attribute] = status;
            return res
        })
        this.forceUpdate()
    }

    /**
     * @description: item
     * @param {type} 
     * @return: 
     */    
    getItemsAttribute = (field, attribute) => {
        let res = this.getStore(['items']).query(field) || {};
        return res[attribute]
    }

    /**
     * @description:      
     * @param {type} 
     * @return: 
     */    
    getAllSearchDataEve = (snapshot, flag, logic = 'and') => {
        let callBackMeta = [];
        //   
        // let snapshot = this.get(['showHighPanel']) ? this.get(['currentHighPlanMap']) : this.get(['currentPlanMap']);
        let multiLang = this.getJson()
        //   	
        let requiredArr = [];

        // for    
        for (let i = 0; i < snapshot.length; i++) {
            if (snapshot[i].children && Array.isArray(snapshot[i].children)) {
                callBackMeta.push(this.getAllSearchDataEve(snapshot[i].children, flag, snapshot[i].operationSign));
                continue;
            }
            let attrcode = snapshot[i].attrcode;
            let val = this.get(['items']).query(attrcode);
            let VALUE = snapshot[i].initialvalue;
            let displayVal = VALUE.display || '';
            let optSign = snapshot[i].operationSign;
            let isIncludeSub = snapshot[i].runWithChildren || false;
            let refurl = snapshot[i].refurl || '';
            let timeFunction = '';

            if (displayVal && snapshot[i].runWithChildren) {
                displayVal = displayVal + multiLang['page-search-0044'];
            }

            let data = {
                firstvalue: '',
                secondvalue: '',
            };

            if (val.required && flag && snapshot[i].visible) {
                let isKong = false;
                if (isEmpty(VALUE.value) || (Array.isArray(VALUE.value) && !ArrayHas2Value(VALUE.value))) {
                    if (optSign != 'is null' && optSign != 'is not null') { isKong = true; }

                }
                if (isKong) {
                    callBackMeta = [];
                    requiredArr.push(val.attrcode);
                }
            }
            if ((val.itemtype === 'rangepicker' || ((val.itemtype === 'datepicker' || val.itemtype === 'NCTZDatePickerEnd' || val.itemtype === 'NCTZDatePickerStart' || val.itemtype === 'datetimepicker') && optSign === 'between')) && VALUE) {
                let vals = VALUE.value;

                if (vals && !isArray(vals)) {
                    vals = vals.split(',');
                }
                //
                if (isDateFunction(vals[0])) {
                    timeFunction = vals.join(',');
                }
                if (vals && isArray(vals)) {
                    if (vals.length > 0 && vals[0] && vals[0].format) {
                        // 
                        let start = vals[0].format('YYYY-MM-DD');
                        let end = vals[1].format('YYYY-MM-DD');
                        // 1 
                        let currentZoneStartTime = moment(start).startOf('day').toDate();
                        let currentZoneStartHours = currentZoneStartTime.getHours();
                        let offsetZone = currentZoneStartTime.getTimezoneOffset() / 60;
                        // 2 
                        let currentZoneEndTime = moment(end).endOf('day').toDate();
                        let currentZoneEndHours = currentZoneEndTime.getHours();
                        // 
                        offsetZone = offsetZone + 8;
                        currentZoneStartTime.setHours(currentZoneStartHours + offsetZone);
                        currentZoneEndTime.setHours(currentZoneEndHours + offsetZone);
                        //   newData.firstvalue = moment(currentZoneStartTime).format('YYYY-MM-DD HH:mm:ss');
                        //   newData.secondvalue = moment(currentZoneEndTime).format('YYYY-MM-DD HH:mm:ss');

                        data.firstvalue = moment(currentZoneStartTime).format('YYYY-MM-DD HH:mm:ss');
                        data.secondvalue = moment(currentZoneEndTime).format('YYYY-MM-DD HH:mm:ss');

                        // newVal.push(value.format('YYYY-MM-DD'));
                    } else if (vals.length > 0 && vals[0] && !vals[0].format) {
                        data.firstvalue = vals[0];
                        data.secondvalue = vals[1];
                    } else if (vals.length === 0) {
                        data.firstvalue = '';
                        data.secondvalue = '';
                    }
                }
            } else if (val.itemtype === 'refer') {
                VALUE = getFunctionValueFromViewModal(VALUE, this.getStore(['context']));
                //    
                if (val.required && flag && snapshot[i].visible) {
                    if (!VALUE.value && VALUE.value !== 0) {
                        callBackMeta = [];
                        if (!requiredArr.includes(val.attrcode)) requiredArr.push(val.attrcode);
                    }
                }
                if (optSign === 'between') {
                    data.firstvalue = (VALUE.value || [])[0] || '';
                    data.secondvalue = (VALUE.value || [])[1] || '';
                    displayVal = (VALUE.display || []).join(',');
                } else {
                    displayVal = VALUE.display;
                    data.firstvalue = VALUE.value;
                }
            } else if (
                (val.itemtype === 'select' ||
                    val.itemtype === 'number' ||
                    val.itemtype === 'datetimepicker' ||
                    val.itemtype === 'NCTZDatePickerEnd' ||
                    val.itemtype === 'NCTZDatePickerStart') &&
                optSign === 'between'
            ) {
                data.firstvalue = (VALUE.value || [])[0];
                data.secondvalue = (VALUE.value || [])[1];
                displayVal = (VALUE.display || [])[0] + '~' + (VALUE.display || [])[1];
            } else {
                data.firstvalue = VALUE.value;
            }
            let timeItemList = [
                'datepicker',
                'datetimepicker',
                'rangepicker',
                'NCTZDatePickerStart',
                'NCTZDatePickerEnd',
                'NCTZDatePickClientTime',
                'NCTZDatePickClientHourTime',
                'NCTZDatePickerRangeDay',
                'datePickerNoTimeZone',
            ];
            // 024024
            const localToEast = ['datepicker', 'datetimepicker', 'NCTZDatePickClientTime', 'NCTZDatePickClientHourTime'];
            //     = 
            const timeStampType = ['NCTZDatePickerStart', 'NCTZDatePickerEnd', 'datetimepicker'];
            if (timeItemList.some(item => item === val.itemtype) && !isEmpty(VALUE.value)) {
                //xuyangt  
                let firstvalue = '',
                    secondvalue = '';
                if (isDateFunction(data.firstvalue) && !data.secondvalue) {
                    timeFunction = data.firstvalue;
                }
                if (isDateFunction(data.firstvalue) || isDateFunction(data.secondvalue)) {
                    if (optSign == '=' || optSign == '<>') {
                        // start  end 
                        if (timeStampType.includes(val.itemtype)) {
                            if (val.itemtype === 'NCTZDatePickerEnd') {
                                firstvalue = timeFunctionTranslater(data.firstvalue, { flag: false });
                            } else {
                                firstvalue = timeFunctionTranslater(data.firstvalue);
                            }
                        } else {
                            firstvalue = timeFunctionTranslater(data.firstvalue);
                            secondvalue = timeFunctionTranslater(data.firstvalue, { flag: false });
                        }
                    } else if (optSign == '<' || optSign == '>=') {
                        firstvalue = timeFunctionTranslater(data.firstvalue);
                    } else if (optSign == '>' || optSign == '<=') {
                        firstvalue = timeFunctionTranslater(data.firstvalue, { flag: false });
                    } else if (optSign == 'between') {
                        if (!isEmpty(data.firstvalue)) {
                            firstvalue = timeFunctionTranslater(data.firstvalue);
                        }
                        if (!isEmpty(data.secondvalue)) {
                            secondvalue = timeFunctionTranslater(data.secondvalue, { flag: false });
                        }
                    }

                } else {
                    if (optSign == '=' || optSign == '<>') {
                        if (!timeStampType.includes(val.itemtype)) {
                            // datepicker datepicker  [00:00:00, 23:59:59]
                            if (localToEast.includes(val.itemtype) && !isDateFunction(data.firstvalue)) {
                                firstvalue = DongbaToLocalTime(moment(data.firstvalue)).format('YYYY-MM-DD 00:00:00');
                                secondvalue = moment(firstvalue).format('YYYY-MM-DD 23:59:59');

                                //  
                                firstvalue = LocalToDongbaTime(moment(firstvalue)).format('YYYY-MM-DD HH:mm:ss');
                                secondvalue = LocalToDongbaTime(moment(secondvalue)).format('YYYY-MM-DD HH:mm:ss');
                            } else {
                                // start end 
                                firstvalue = moment(data.firstvalue).format('YYYY-MM-DD 00:00:00');
                                secondvalue = timeFunctionTranslater(moment(data.firstvalue).format('YYYY-MM-DD 23:59:59'));
                            }
                        } else {
                            firstvalue = data.firstvalue;
                            secondvalue = firstvalue;
                        }

                    } else if (optSign == '<' || optSign == '>=') {
                        firstvalue = naturalTimeHandler(data.firstvalue, 'start');
                    } else if (optSign == '>' || optSign == '<=') {
                        firstvalue = naturalTimeHandler(data.firstvalue, 'end');
                    } else if (optSign == 'between') {
                        if (!isEmpty(data.firstvalue)) {
                            firstvalue = naturalTimeHandler(data.firstvalue, 'start');
                        }
                        if (!isEmpty(data.secondvalue)) {
                            secondvalue = naturalTimeHandler(data.secondvalue, 'end');
                        }
                    }
                    //
                    if (firstvalue && secondvalue && CompareDate(firstvalue, secondvalue)) {
                        firstvalue = timeFunctionTranslater(secondvalue);
                        secondvalue = timeFunctionTranslater(firstvalue, { flag: false });
                    }
                }
                if (optSign == '=') {
                    optSign = 'between';
                }
                data.firstvalue = firstvalue;
                data.secondvalue = secondvalue;

            }
            if (!val.hasOwnProperty('queryOperateType')) {
                val.queryOperateType = '';
                console.error(`queryOperateType${val.attrcode}`);
            }
            //   
            if (data.firstvalue && typeof data.firstvalue === 'string' && data.firstvalue !== ' ') {
                data.firstvalue = data.firstvalue.trim();
            }
            if (data.secondvalue && typeof data.firstvalue === 'string' && data.secondvalue !== ' ') {
                data.secondvalue = data.secondvalue.trim();
            }
            let Obj = {
                field: attrcode,
                value: data,
                oprtype: optSign,
                display: String(displayVal || '').replace(/~/, ','),
                isIncludeSub,
                refurl,
                datatype: val.datatype || '',
                timeFunction,
            };
            if (val.itemtype === 'residtxt') {
                Obj.langSeq = VALUE.index;
            }
            if (!isEmpty(Obj.value.firstvalue) || !isEmpty(Obj.value.secondvalue) || optSign == 'is null' || optSign == 'is not null') {
                snapshot[i].visible && callBackMeta.push(Obj);
            }
        }

        // if (requiredArr.length !== 0) {
        //     {
        //         toast({ content: multiLang['page-search-0045'], color: 'warning' });
        //     }
        //     return false;
        // }

        return {
            logic,
            conditions: callBackMeta,
        };
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    getAllSearchData = (flag = true) => {
        let snapshot = this.get(['showHighPanel']) ? this.get(['currentHighPlanMap']) : this.get(['currentPlanMap']);
        return this.getAllSearchDataEve(snapshot, flag);
    }

    /**
     * @description: getAllSearchData
     * @param {type} 
     * @return: 
     */    
    getQueryInfo(flag = true) {
        let data = this.getAllSearchData(flag);
        if (data) {
            return {
                queryAreaCode: this.getStore(['searchId']),
                oid: this.getStore(['oid']),
                querytype: 'tree',
                querycondition: data,
                nctz: getTimezoneOffset(),
            };
        } else {
            return {};
        }
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setSearchValByField = (field, data, status) => {
        // status  
        let currentMap, currentStatus;
        if(status && ['normal', 'super'].includes(status)){
            if(status === 'normal'){
                currentMap = this.getStore(['currentPlanMap']);
                currentStatus = 'currentPlanMap'
            }else {
                currentMap = this.getStore(['currentHighPlanMap'])
                currentStatus = 'currentHighPlanMap'
            }
            
        }else {
            if(!this.getStore('showHighPanel')){
                currentMap = this.getStore(['currentPlanMap']);
                currentStatus = 'currentPlanMap'
            }else {
                currentMap = this.getStore(['currentHighPlanMap'])
                currentStatus = 'currentHighPlanMap'
            }
        }
        if(currentMap){
            if (!data) {
                data = {
                    value: '',
                    display: '',
                };
            }
            setValue(currentMap, field, data);
            this.setStore([currentStatus], currentMap)
        }
    }

    /**
     * @description:  getAllSearchData  
     * @param {type} 
     * @return: 
     */    
    setSearchValue = (data, callback) => {
        let currentStatus = this.getStore(['showHighPanel']) ? 'currentHighPlanMap' : 'currentPlanMap';
        let snapshot = this.getStore([currentStatus])
        let items = snapshot;
        let datas = data;
        if (datas.conditions) {
            datas = datas.conditions;
        }
        if (!isArray(datas)) {
            console.error('setSearchValue');
            return false;
        }
        const findItem = item => {
            let len = items.length;
            while (len--) {
                if (items[len].attrcode === item.field) {
                    items[len].initialvalue = items[len].initialvalue || {};
                    if (item.oprtype === 'between') {
                        items[len].initialvalue.value = [item.value.firstvalue, item.value.secondvalue];
                        //items[len].initialvalue.display = [item.value.firstvalue, item.value.secondvalue];
                    } else {
                        items[len].initialvalue.value = item.value.firstvalue;
                        items[len].initialvalue.display = item.display;
                    }
                    // bug
                    if (item.timeFunction) {
                        if (item.timeFunction.includes(',')) {
                            items[len].initialvalue.value = item.timeFunction.split(',');
                        } else {
                            items[len].initialvalue.value = item.timeFunction;
                            items[len].initialvalue.display = '';
                        }
                    }
                    items[len].operationSign = item.oprtype;
                    items[len].runWithChildren = item.isIncludeSub || false;
                    items[len].refurl = item.refurl || '';
                    break;
                }
            }
        };
        datas.map(item => {
            findItem(item);
        });
        this.setStore([currentStatus], items);
        typeof callback === 'function' && callback();
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    getSearchValByField = (field, status) => {
        // status status 
        let currentMap;
        if(status && ['normal', 'super'].includes(status)){
            if(status === 'normal'){
                currentMap = this.getStore(['currentPlanMap']);
            }else {
                currentMap = this.getStore(['currentHighPlanMap'])
            }
            
        }else {
            if(!this.getStore('showHighPanel')){
                currentMap = this.getStore(['currentPlanMap']);
            }else {
                currentMap = this.getStore(['currentHighPlanMap'])
            }
        }
        let data = this.getAllSearchDataEve(currentMap, false);
        if (!data) {
            return { value: { firstvalue: '', secondvalue: '' }, display: '', oprtype: '' };
        }
        let len = data.conditions.length;
        if (len <= 0) {
            return { value: { firstvalue: '', secondvalue: '' }, display: '', oprtype: '' };
        }
        // 
        let res = { value: { firstvalue: '', secondvalue: '' }, display: '', oprtype: '' };

        function getvalue(list, field) {
            list.forEach(item => {
                if (item.conditions) {
                    getvalue(item.conditions, field);
                } else {
                    if (item.field === field) {
                        res.value = item.value;
                        res.display = item.display;
                        res.oprtype = item.oprtype;
                    }
                }
            });
        }
        getvalue(data.conditions, field);
        return res;
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setDisabledByField = (field, status) => {
        this.setItemsAttribute(field, 'disabled', status)
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    closeSearchPlanArea = () => {
        this.setStore(['highPanelStatus'], false)
    }

    /** / 
     * @description: 
     * @param {type} 
     * @return: 
     */    
    openAdvSearch = (status, callback) => {
        this.setStore(['highPanelStatus'], false)
        typeof callback == 'function' && callback()
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setDisabled = (moduleId, status) => {
        let items = this.getStore(['items']);
        items.forEach(item => {
            item['disabled'] = status
        })
        this.setStore(['items'], items)
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    clearSearchArea = () => {
        let currentMap, currentStatus;
        if(!this.getStore('showHighPanel')){
            currentMap = this.getStore(['currentPlanMap']);
            currentStatus = 'currentPlanMap'
        }else {
            currentMap = this.getStore(['currentHighPlanMap'])
            currentStatus = 'currentHighPlanMap'
        }
        let len = currentMap.length;
        while (len--) {
            currentMap[len].initialvalue = {
                value: '',
                display: '',
            };
        }
        this.setStore([currentStatus], currentMap)
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    getOprtypeByField = field =>{ 
        let currentMap = this.get(['showHighPanel']) ? this.get(['currentHighPlanMap']) : this.get(['currentPlanMap']);
        function getValue(list, result = []) {
            list.forEach(item => {
                if (item.children) {
                    getValue(item.children, result);
                } else {
                    if (item.attrcode === field) {
                        result.push(item.operationSign);
                    }
                }
            });
            return result;
        }

        return getValue(currentMap);
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */     
    changeItemVisibleByField = (field, status) => {

    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setRequiredByField = (field, status) => {
        this.setItemsAttribute(field, 'required', status)
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    setTemlateByField = (field, property, value) => {
        this.setItemsAttribute(field, property, value)
    }

    /**
     * @description: 
     * @param {type} 
     * @return: 
     */    
    clearAllValueEve = () => {
        let currentMap = this.get(['showHighPanel']) ? this.get(['currentHighPlanMap']) : this.get(['currentPlanMap']);
        let itemMap = this.get(['items'])
        clearAllValueEve(currentMap, itemMap);
        this.forceUpdate();
    }

}

export const useSearch = hookFactory(SearchStore)